<template>
  <div class="login_container" @keyup.enter="handleLoginEvent">
    <div class="login_box">
      <div class="margin-top-50">
        <img
          :src="require('../assets/img/Login/login_icon_Shattrath.png')"
          alt="沙塔信息技术有限公司"
        />
      </div>
      <div class="margin-bottom-30">
        <h1>用户登录</h1>
      </div>
      <div class="margin-bottom-20">
        <el-input
          ref="accountInput"
          class="width-360 login-input"
          suffix-icon="login-account"
          placeholder="用户名"
          v-model.trim="loginAccount"
          clearable
        ></el-input>
      </div>
      <div class="margin-bottom-15">
        <el-input
          type="password"
          ref="passwordInput"
          class="width-360 login-input"
          suffix-icon="login-password"
          placeholder="密码"
          v-model.trim="loginPassword"
          clearable
        ></el-input>
      </div>
      <div class="margin-bottom-40 display-grid">
        <el-checkbox class="justify-self-start" v-model="isPasswordKept"
          >记住密码</el-checkbox
        >
        <div class="justify-self-end">
          <el-link>忘记密码？</el-link>
        </div>
      </div>
      <div>
        <el-button class="width-360" type="primary" @click="handleLoginEvent"
          >立即登录</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE } from '@/utils/constantsFile';
// 导入所需接口
import { login } from '@/api/public';
export default {
  name: 'Login',

  data() {
    return {
      // 用户名
      loginAccount: '',

      // 密码
      loginPassword: '',

      // 是否记住密码
      isPasswordKept: true
    };
  },

  methods: {
    /**
     * @description: “立即登录”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleLoginEvent() {
      // 提示用户名不能为空
      if (!this.loginAccount) {
        showWarningMessage('用户名不能为空！');
        this.$refs.accountInput.focus();
        return false;
      }

      // 提示密码不能为空
      if (!this.loginPassword) {
        showWarningMessage('密码不能为空！');
        this.$refs.passwordInput.focus();
        return false;
      }
      this.login();
    },
    async login() {
      try {
        this.tableLoading = true;
        const res = await login(
          {},
          {
            userName: this.loginAccount,
            password: this.loginPassword
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          localStorage.setItem('token', JSON.stringify(res.rows.token));
          localStorage.setItem('realName', JSON.stringify(res.rows.realName));
          localStorage.setItem('userId', JSON.stringify(res.rows.userId));
          this.$router.push('/workstation/home');
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },

  mounted() {
    // 用户名输入框自动获取焦点
    this.$refs.accountInput.focus();
  }
};
</script>

<style lang="less" scoped>
.login_container {
  background-color: #0871dc;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_box {
  width: 450px;
  height: 550px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.margin-top-50 {
  margin-top: 50px;
}

.width-360 {
  width: 360px;
}

.margin-bottom-40 {
  margin-bottom: 40px;
}

.margin-bottom-30 {
  margin-bottom: 30px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}

.margin-bottom-15 {
  margin-bottom: 15px;
}

.display-grid {
  display: grid;
  grid-template-columns: 180px 180px;
}

// “记住密码”checkbox靠左对齐
.justify-self-start {
  justify-self: start;
}

// “忘记密码”链接靠右对齐
.justify-self-end {
  justify-self: end;
}

// 调整输入框中的icon的位置
.login-input /deep/ .el-input__icon {
  margin-top: 4px;
}
</style>
